import uniIcons from '../uni-icons/uni-icons.vue'

/**
 * Pagination 分页器
 * @description 分页器组件，用于展示页码、请求数据等
 * @tutorial https://ext.dcloud.net.cn/plugin?id=32
 * @property {String} prevText 左侧按钮文字
 * @property {String} nextText 右侧按钮文字
 * @property {Number} current 当前页
 * @property {Number} total 数据总量
 * @property {Number} pageSize 每页数据量
 * @property {Number} showIcon = [true|false] 是否以 icon 形式展示按钮
 * @event {Function} change 点击页码按钮时触发 ,e={type,current} current为当前页，type值为：next/prev，表示点击的是上一页还是下一个
 */

export default {
	name: 'UniPagination',
	components: {
		uniIcons
	},
	props: {
		prevText: {
			type: String,
			default: '上一页'
		},
		nextText: {
			type: String,
			default: '下一页'
		},
		current: {
			type: [Number, String],
			default: 1
		},
		total: { // 数据总量
			type: [Number, String],
			default: 0
		},
		pageSize: { // 每页数据量
			type: [Number, String],
			default: 10
		},
		showIcon: { // 是否以 icon 形式展示按钮
			type: [Boolean, String],
			default: false
		}
	},
	data() {
		return {
			currentIndex: 1
		}
	},
	computed: {
		maxPage() {
			let maxPage = 1
			let total = Number(this.total)
			let pageSize = Number(this.pageSize)
			if (total && pageSize) {
				maxPage = Math.ceil(total / pageSize)
			}
			return maxPage
		}
	},
	watch: {
		current(val) {
			this.currentIndex = +val
		}
	},
	created() {
		this.currentIndex = +this.current
	},
	methods: {
		clickLeft() {
			if (Number(this.currentIndex) === 1) {
				return
			}
			this.currentIndex -= 1
			this.change('prev')
		},
		clickRight() {
			if (Number(this.currentIndex) === this.maxPage) {
				return
			}
			this.currentIndex += 1
			this.change('next')
		},
		change(e) {
			this.$emit('change', {
				type: e,
				current: this.currentIndex
			})
		}
	}
}
